body {
  color: var(--primary-text-color);
  background-color: #f5f5f5;
  background-image: url("Background.11f93cd0.png");
  background-size: cover;
  margin: 0;
  font-family: Montserrat Alternates;
  font-weight: 600;
}

main {
  padding-left: 175px;
  padding-right: 175px;
}

*, :before, :after {
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: all .25s linear;
}

h1, h2, h3, p, ul {
  margin: 0;
  padding: 0;
}

a {
  color: var(--primary-text-color);
  margin: 0;
  padding: 0;
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.section {
  z-index: 2;
  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  position: relative;
}

.section.isNotActive {
  display: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.section-header {
  height: 80px;
}

.container-header {
  background-color: #fff;
  align-items: center;
  height: 100%;
  padding-left: 175px;
  padding-right: 175px;
  display: flex;
}

.logo-link {
  width: 95px;
  height: 40px;
  display: inline-block;
}

.logo-svg {
  width: 100%;
  height: 100%;
}

.nav-list {
  scroll-behavior: smooth;
  justify-content: space-between;
  width: 370px;
  margin-left: 81px;
  font-size: 12px;
  line-height: 15px;
  display: flex;
}

.nav-item-interactive {
  position: relative;
}

.burger-menu {
  border: 1px solid #000;
  border-radius: 0 0 20px 20px;
  width: 160px;
  height: 105px;
  display: none;
  position: absolute;
  left: -35%;
  overflow: hidden;
}

.burger-menu-item {
  height: 35px;
}

.burger-menu-link {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: inline-flex;
}

.burger-menu-link.picked {
  background: #f1f1f1;
}

.burger-menu.isOpen {
  display: block;
}

.light-mode-button {
  cursor: pointer;
  background: gray;
  border: none;
  border-radius: 11px;
  width: 40px;
  height: 20px;
  margin-left: 361px;
  padding: 0;
  position: relative;
}

.light-mode-button-circle {
  background-color: #fff;
  background-image: url("sun.fdca2916.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 15px 15px;
  border: 1px solid #000;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: all .25s linear;
  position: absolute;
  top: 0;
  left: 0;
}

.light-mode-button:hover > .light-mode-button-circle {
  background-color: #000;
  background-image: url("moon.496dbf79.svg");
  left: 20px;
}

.header-name-text {
  margin-left: 51px;
  font-size: 12px;
  font-style: normal;
  line-height: 15px;
}

.main-title-article {
  background-color: #fff;
  padding-top: 36px;
  padding-bottom: 36px;
}

.main-title {
  width: 390px;
  margin-left: auto;
  margin-right: auto;
  font-size: 24px;
  line-height: 29px;
}

.second-title {
  text-align: center;
  width: 330px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  line-height: 20px;
}

.third-title {
  text-align: center;
  margin-top: 30px;
  font-size: 10px;
}

.Leap-year-calculator-section, .Guess-the-number-section {
  z-index: 2;
  padding: 36px 257px;
  position: relative;
}

.Leap-year-calculator-container, .Guess-the-number-container {
  height: 100%;
  position: relative;
}

.leap-year-form, .guess-number-form {
  border-radius: 20px;
  align-items: end;
  width: 209px;
  height: 35px;
  margin-top: 26px;
  margin-left: 40px;
  display: flex;
  box-shadow: 3px 3px #00000040;
}

.guess-number-form {
  width: 135px;
}

.leap-year-input, .guess-number-input, .time-calc-input {
  color: #7e7e7e;
  background-color: #d7d7d7;
  border: none;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  outline: none;
  padding: 10px;
  font-size: 12px;
  line-height: 15px;
}

.guess-number-input, .time-calc-input {
  width: 100px;
}

.leap-year-button, .guess-number-button, .time-calc-button {
  cursor: pointer;
  background-color: #000;
  background-image: url("search.0a9dc09d.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25px 25px;
  border: none;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  width: 35px;
  height: 35px;
  padding: 0;
}

.leap-year-text, .guess-number-text, .time-calc-text {
  z-index: 0;
  width: 230px;
  font-size: 12px;
  line-height: 15px;
  transition: all .25s linear;
  position: absolute;
  top: 46px;
  right: 0;
}

.Rock-paper-scissors-container {
  margin-left: 220px;
  margin-right: 220px;
  position: relative;
}

.rps-list {
  justify-content: space-between;
  width: 260px;
  height: 60px;
  margin-top: 36px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.rps-item {
  border-radius: 20px;
  width: 60px;
  height: 100%;
}

.rock, .paper, .scissors {
  cursor: pointer;
  background-color: #000;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  transition: all .25s linear;
}

.rock {
  background-image: url("stone.bb20d970.svg");
}

.paper {
  background-image: url("paper.768b3283.svg");
}

.scissors {
  background-image: url("scissors.ebd16b4f.svg");
}

.rps-button {
  background-position: center;
  background-size: 40px 40px;
}

.rps-button:hover {
  scale: 1.1;
}

.rps-button-picked {
  border: 4px solid gray;
  scale: 1.3;
}

.rps-button-picked:hover {
  scale: 1.3;
}

.rps-button-ai-picked {
  background-color: #900;
}

.rps-text {
  text-align: center;
  width: 220px;
  margin-top: 36px;
  margin-left: auto;
  margin-right: auto;
  font-size: 12px;
  line-height: 15px;
  transition: all .25s linear;
}

.rps-score {
  z-index: 0;
  width: 100px;
  font-size: 12px;
  line-height: 170%;
  position: absolute;
  top: 56px;
  right: 0;
}

.rps-start-button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 20px;
  margin-top: 36px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  display: flex;
}

.rps-start-button:disabled {
  color: #2a2a2a;
  background-color: #d3d3d3;
  box-shadow: inset 0 0 10px 5px #00000030;
}

.Calculator-section {
  padding: 36px 230px;
}

.op-form {
  justify-content: space-between;
  align-items: center;
  margin-top: 36px;
  display: flex;
}

.op-num1, .op-num2, .op-result {
  box-sizing: content-box;
  background: #d7d7d7;
  border: none;
  border-radius: 20px;
  outline: none;
  width: 95px;
  height: 15px;
  padding: 10px 20px;
  box-shadow: 3px 3px #00000040;
}

.op-list {
  flex-wrap: wrap;
  width: 80px;
  height: 80px;
  margin-top: -10px;
  margin-right: -10px;
  display: flex;
}

.op-item {
  width: calc(50% - 10px);
  height: calc(50% - 10px);
  margin-top: 10px;
  margin-right: 10px;
}

.plus, .minus, .multiply, .divide, .op-equal {
  cursor: pointer;
  color: #fff;
  box-sizing: content-box;
  background-color: #000;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 17px;
  line-height: 15px;
  transition: all .25s linear;
  display: flex;
  box-shadow: 3px 3px #00000040;
}

.plus, .minus, .multiply, .divide {
  width: 100%;
  height: 100%;
}

.op-equal {
  width: 30px;
  height: 30px;
}

.op-equal:disabled {
  background-color: gray;
}

.picked-operation {
  color: #000;
  background-color: #fff;
  border: 2px solid #000;
}

.op-result {
  color: #7e7e7e;
  box-shadow: none;
  width: min-content;
  font-size: 12px;
  line-height: 15px;
}

.Time-calculator-section {
  padding: 36px 300px;
}

.Time-calculator-container {
  position: relative;
}

.time-calc-form {
  margin-top: 36px;
  display: flex;
}

.time-calc-select {
  margin-left: 50px;
}

.time-calc-text {
  top: 65px;
  right: 0;
}

.Football-section {
  padding: 36px 185px;
}

.football-field {
  box-sizing: border-box;
  cursor: url("football-cursor.5acfa01f.svg"), auto;
  background: #5abb58;
  border: 2px solid #000;
  border-radius: 37px;
  width: 100%;
  height: 260px;
  margin-top: 36px;
  position: relative;
  overflow: hidden;
}

.ball {
  background-image: url("ball.1d943e83.svg");
  background-size: cover;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  transition: all .5s linear;
  animation-duration: .5s;
  animation-timing-function: linear;
  position: absolute;
  top: 85px;
  left: 92px;
}

@keyframes ball-moving {
  0% {
    transform: scale(1)rotate(0);
  }

  50% {
    transform: scale(2)rotate(180deg);
  }

  100% {
    transform: scale(1)rotate(360deg);
  }
}

@keyframes ball-moving2 {
  0% {
    transform: scale(1)rotate(0);
  }

  50% {
    transform: scale(2)rotate(180deg);
  }

  100% {
    transform: scale(1)rotate(360deg);
  }
}

.Tic-Tac-Toe-section {
  padding: 36px 300px;
}

.ttt-list {
  border: 3px solid #000;
  flex-wrap: wrap;
  width: 300px;
  height: 300px;
  margin: 36px auto 25px;
  display: flex;
}

.ttt-item {
  width: 33.3333%;
}

.ttt-text {
  text-align: center;
  margin-bottom: 20px;
  font-size: 15px;
  font-weight: 400;
}

.ttt-button {
  box-sizing: border-box;
  cursor: pointer;
  border: 3px solid #000;
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
}

.ttt-button:disabled {
  cursor: auto;
}

.ttt-game-active:hover {
  opacity: .5;
  background-image: url("tic.d6f652b0.svg");
}

.ttt-button-picked {
  background-image: url("tic.d6f652b0.svg");
}

.ttt-button-ai-picked {
  background-image: url("tac.81667bf9.svg");
}

.ttt-start-button, .ttt-ai-pick-button {
  color: #7e7e7e;
  cursor: pointer;
  background: #d7d7d7;
  border: none;
  border-radius: 20px;
  width: 135px;
  height: 40px;
  padding: 10px 20px;
  box-shadow: 3px 3px #00000040;
}

.ttt-start-button {
  margin-left: 20px;
  margin-right: 250px;
}

.ttt-ai-pick-button:disabled, .ttt-start-button:disabled {
  cursor: auto;
  color: #d7d7d7;
  background-color: #7e7e7e;
}

.Highest-number-section {
  padding: 36px 305px;
}

.biggest-number-form {
  justify-content: space-between;
  margin-top: 36px;
  margin-bottom: 36px;
  display: flex;
}

.biggest-num-input {
  background: #d7d7d7;
  border: none;
  border-radius: 20px;
  outline: none;
  width: 135px;
  height: 35px;
  padding: 10px 20px;
  box-shadow: 3px 3px #00000040;
}

.biggest-num-text {
  text-align: center;
  font-size: 12px;
  line-height: 15px;
}

.Team-section {
  padding: 36px 92px;
}

.Team-container {
  justify-content: space-between;
}

.team-list-container {
  justify-content: space-between;
  align-items: center;
  height: 230px;
  margin-top: 36px;
  display: flex;
  position: relative;
}

.team-button {
  cursor: pointer;
  background-color: #fff;
  background-image: url("next.fdff24f1.svg");
  background-repeat: no-repeat;
  border: none;
  width: 50px;
  height: 100px;
  padding: 0;
}

.right-team-button {
  transform: rotate(180deg);
}

.team-item {
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  transition: all 1s linear;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.team-item-left {
  opacity: 0;
  transition: all 1s linear;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(50%, -50%)scale(.5);
}

.team-item-right {
  opacity: 0;
  transition: all 1s linear;
  transform: translate(125%, -50%)scale3d(.5, .5, 1);
}

.team-item > img {
  background: #d9d9d9;
  border: none;
  border-radius: 20px;
  width: 150px;
  height: 150px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
}

.team-item > h3 {
  font-weight: 400;
}

.team-title {
  text-align: center;
  font-size: 14px;
  line-height: 17px;
}

.team-text {
  text-align: center;
  margin-top: 20px;
  font-size: 12px;
  line-height: 15px;
}

.main-team-cont {
  justify-content: space-between;
  width: 220px;
  margin-top: 16px;
  display: flex;
  position: absolute;
  bottom: -24px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.team-cont {
  background: #d9d9d9;
  border-radius: 3px;
  width: 18px;
  height: 3px;
  transition: all .25s linear;
}

.team-cont-picked {
  background: #797979;
  width: 30px;
  transition: all .25s linear;
}

.Scientist-section {
  padding: 36px 257px;
}

.scientists-list {
  text-align: center;
  grid-template-columns: repeat(4, 1fr);
  gap: 36px 59px;
  margin: 26px 0;
  display: grid;
}

.scientist-item {
  background: #d9d9d9 0 0 / cover;
  border-radius: 20px;
  align-content: center;
  height: 100px;
  font-size: 10px;
  line-height: 170%;
  transition: all .25s linear;
  animation-duration: 1s;
  animation-timing-function: linear;
  display: grid;
  position: relative;
}

.scientists-btn {
  cursor: pointer;
  background: #000;
  border-radius: 20px;
  width: 100px;
  height: 30px;
  box-shadow: 3px 3px #00000040;
}

@keyframes showImages {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(360deg);
  }
}

.scientist-item-picked {
  transition: all .25s linear;
  transform: scale(1.2);
}

.scientist-item-deleted {
  background-position: center;
  transition: all .25s linear;
  transform: scale(1.2);
}

.task-text {
  text-align: center;
  margin-top: 20px;
  font-size: 12px;
  line-height: 15px;
}

.buttons-container {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.scientists-btn {
  color: #fff;
  box-sizing: border-box;
  border: none;
  width: 440px;
  height: 35px;
  margin-bottom: 20px;
  font-size: 12px;
  line-height: 15px;
}

.true {
  border: 5px solid #039900;
}

.false {
  border: 5px solid #900;
}

.didnt-pick {
  border: 5px solid #00f;
  scale: 1.2;
}

.didnt-pick-list {
  border: 5px solid #00f;
}

.clue-text {
  background-color: #fff;
  width: 100%;
}

.true-clue-text {
  border-radius: 10px;
  position: absolute;
  top: -22px;
  left: 50%;
  transform: translateX(-50%);
}

.clue-text-list {
  background-color: #fff;
  width: 100%;
}

.wrong-answer {
  text-decoration: line-through;
}

.section-footer {
  background-color: #fff;
  padding: 60px 175px;
}

.container-footer {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.container-footer > .logo-link {
  width: 165px;
  height: 70px;
}

.container-footer use {
  scale: 2;
}

.contact-list {
  font-size: 12px;
  line-height: 170%;
}

.footer-form__input {
  background: #d7d7d7;
  border: none;
  border-radius: 20px;
  padding: 10px 80px 10px 10px;
}

.footer-form__button {
  color: #fff;
  cursor: pointer;
  background: #000;
  border: none;
  border-radius: 20px;
  margin-left: 36px;
  padding: 10px;
}

.footer-form__text {
  margin-top: 18px;
  font-size: 7px;
  line-height: 7px;
}

.backdrop {
  z-index: 10;
  background-color: #00000080;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  transition: all .1s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.isNotActive {
  pointer-events: none;
  opacity: 0;
  transition: all .1s linear;
}

body.backdrop-active {
  overflow: hidden;
}

.modal-window {
  background-color: #fff;
  background-image: url("sword.b4267e5f.svg"), url("puzzle.e8a32fef.svg"), url("game-development.bfd9510c.svg"), url("tic-tac-toe.3ec97000.svg");
  background-position: 41px 154px, 85px 230px, 505px 154px, 461px 230px;
  background-repeat: no-repeat;
  background-size: 40px 40px;
  border-radius: 20px;
  width: 576px;
  height: 316px;
  padding: 46px 92px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-window__text {
  color: #000;
  text-align: center;
  margin-bottom: 42px;
  font-size: 12px;
  font-style: normal;
  line-height: normal;
}

.modal-window__form {
  flex-direction: column;
  grid-template-columns: 170px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal-window__label {
  text-align: center;
  margin-bottom: 18px;
  font-size: 15px;
  font-style: normal;
  line-height: normal;
}

.modal-window__input {
  background: #d7d7d7;
  border: none;
  border-radius: 20px;
  width: 170px;
  margin-bottom: 18px;
  padding: 10px 90px 10px 10px;
}

.modal-window__button {
  color: #fff;
  cursor: pointer;
  background: #000;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  box-shadow: 3px 3px #00000040;
}

.modal-window-subscribe {
  background-position: 43px 69px, 87px 145px, 391px 69px, 347px 145px;
  background-size: 30px 30px;
  width: 463px;
  height: 245px;
}

.modal-window-subscribe > p {
  text-align: center;
  margin-top: 37px;
  margin-bottom: 0;
  font-size: 20px;
}

.close-modal-button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  width: 17px;
  height: 17px;
  padding: 0;
  transition: all .25s linear;
  position: absolute;
  top: 30px;
  right: 30px;
}

.close-modal-button:hover, .close-modal-button:focus {
  rotate: 180deg;
}

.close-modal-button__svg {
  width: 100%;
  height: 100%;
  position: relative;
}

.light-mode-button.darkMode > .light-mode-button-circle {
  background-color: #000;
  background-image: url("moon.496dbf79.svg");
  left: 20px;
}

.light-mode-button.darkMode:hover > .light-mode-button-circle {
  background-color: #fff;
  background-image: url("sun.fdca2916.svg");
  top: 0;
  left: 0;
}

body.darkMode {
  background-color: #000;
  background-image: none;
}

body.darkMode * {
  transition: all .25s linear;
}

body.darkMode section, body.darkMode article, body.darkMode .container-header {
  background-color: #333;
}

body.darkMode p, body.darkMode span, body.darkMode a, body.darkMode h1, body.darkMode h2, body.darkMode h3, body.darkMode h4 {
  color: #fff;
}

body.darkMode svg {
  fill: #fff;
  background-color: #333;
}

body.darkMode .interactive-link:after {
  color: #fff;
}

body.darkMode .burger-menu {
  border: 2px solid #fff;
}

body.darkMode .burger-menu-link.picked {
  color: #000;
}

body.darkMode .leap-year-text, body.darkMode .guess-number-text, body.darkMode .op-result {
  color: #000;
  text-align: center;
  background-color: #d7d7d7;
  border-radius: 10px;
  padding: 5px;
  box-shadow: 3px 3px #00000040;
}

body.darkMode .op-result {
  border-radius: 20px;
  padding: 10px;
}

body.darkMode .football-field {
  border: 3px solid #fff;
